import React from 'react';

import Programming1 from '../../assets/img/programming1.jpg';
import Programming2 from '../../assets/img/programming2.jpg';
import Programming3 from '../../assets/img/programming3.jpg';
import ProgrammingGif from '../../assets/img/programmingGif.gif';
import ProgrammingLecturer from '../../assets/img/programmingLecturer.png';
import ProgrammingLecturerGif from '../../assets/img/programmingLecturerGif.gif';
import ScienceArrow from '../../assets/img/scienceArrow.svg';
import ProgrammingFrame from '../../assets/img/programmingFrame.png';
import { Link } from 'react-router-dom';
import Footer from '../../components/Footer';
import ScienceMail from '../../components/ScienceMail';

const ScienceProgramming = () => {
	return (
		<div>
			<div className='science-background science-background--programming'>
				<h2 data-aos='fade-up'>PROGRAMOWANIE</h2>
				<Link to='/nauki-scisle-po-godzinach'>NAUKI CISE PO GODZINACH</Link>
			</div>
			<div className='science-content'>
				<h3>
					Dlaczego warto uczy si
					<br className='br-reverse' /> programowania?
				</h3>
				<div className='science-programming section-1'>
					<div className='section'>
						<ul className='science-text'>
							<li>
								<bold className='science-color-programming-bigger'>
									poprawa umiejtnoci analitycznych
								</bold>
							</li>
							Przede wszystkim to wietny sposb na popraw umiejtnoci
							analitycznych. Pokazuje jak rozwizywa wiksze problemy poprzez
							rozoenie je na mniejsze czci. Jest to bardzo cenna umiejtno
							na wspczesnym rynku pracy.
							<li>
								<bold className='science-color-programming-bigger'>
									rozwijanie kluczowych kompetencji
								</bold>
							</li>
							Programowanie i mylenie komputatywne to obecnie kluczowe
							kompetencje. Nauka, chociaby podstaw, jakiegokolwiek jzyka
							programowania oraz umiejtno rozwizywania problemw krok po
							kroku zdaj si by wanymi elementami edukacji.
							<li>
								<bold className='science-color-programming-bigger'>
									satysfakcja i rozwj
								</bold>
							</li>
							Ponadto programowanie moe pomc w znalezieniu dobrze patnego
							stanowiska, gwarantujcego satysfakcj i rozwj.
							<li>
								<bold className='science-color-programming-bigger'>zabawa</bold>
							</li>
							Co jednak najwaniejsze, programowanie i rozwizywania problemw
							algorytmicznych moe dostarczy duo zabawy i zadowolenia modemu
							czowiekowi.
						</ul>
					</div>
					<div className='section section-img'>
						<img src={Programming1} alt='Zajcia z programowania.' />
					</div>
				</div>
			</div>
			<div className='science-separator programming-separator'></div>
			<div className='science-content'>
				<h3>PROGRAMOWANIE PYTHON</h3>
				<div className='science-programming section-2'>
					<div className='section'>
						<p className='science-text'>
							Prawdopodobnie na lekcjach informatyki Pastwa Dziecko poznao ju
							takie programy jak Scratch czy Baltie. S one dobrym wstpem do
							rozpoczcia programowania w dorosym jzyku.
							<br />
							<br />
							Na naszych zajciach uczniowie rozpoczn swoj przygod z jzykiem
							Python, ktry obecnie jest jednym z najpopularniejszych jzykw
							programowania, a jednoczenie zapewnia przyjazny start w
							zaawansowane kodowanie.
							<br />
							<br />
							<bold className='science-color-programming-bigger'>
								Poziom zaj dostosowany jest do poziomu uczestnikw.
							</bold>
							<br />
							<br />
							<ul>
								<li>
									Ci, ktrzy zaczynaj swoj przygod z jakimkolwiek
									programowaniem, otrzymaj przyjazny start i duo sposobw na
									rozwinicie skrzyde.{' '}
								</li>
								<li>
									Ci, ktrzy ju co potrafi dostan stymulujcy zastrzyk
									nowych pomysw i wyzwa, ktre pomog im si rozwin.
								</li>
							</ul>
							Wszystko za w atmosferze wolnej od ocen i popiechu.
						</p>
					</div>
					<div className='section section-img'>
						<img src={Programming2} alt='Zajcia z programowania.' />
					</div>
					<div className='section'>
						<p className='science-text science-text-bigger'>
							Zajcia skierowane s do{' '}
							<bold>modych ludzi od VII klasy szkoy podstawowej</bold>
							<br />
							<br />
							Zapisy przyjmujemy cay rok szkolny.
							<br />
							<br /> Pierwsze grupy startuj w <bold>padzierniku 2022 r.</bold>
							<br />
							<br />
							Zajcia odbywaj si:
						</p>
						<div
							className='science-classes classes-programming'
							data-aos='fade-up'
						>
							<div>w maych grupch</div>
							<div>raz w tygodniu</div>
							<div>trwaj 90 minut</div>
						</div>
					</div>
				</div>
			</div>
			<div className='science-separator programming-separator'></div>
			<div className='science-content'>
				<h3>PRZYGOTOWANIE DO MATURY Z INFORMATYKI</h3>
				<div className='science-programming section-2'>
					<div className='section'>
						<p className='science-text'>
							Uczniowie szk ponadpodstawowych chccy zdawa matur z
							informatyki znajd na naszych zajciach kogo,
							<br />
							<br />
							<bold className='science-color-programming-bigger'>
								kto pomoe im si przygotowa, wyjani trudnoci w zadaniach
								oraz bdzie motywowa ich do wytonego wysiku.
							</bold>
						</p>
					</div>
					<div className='section section-img'>
						<img src={Programming3} alt='Zajcia z programowania.' />
					</div>
					<div className='section'>
						<p className='science-text science-text-bigger'>
							Zajcia skierowane s do{' '}
							<bold>
								uczniw chccych zdawa informatyk na poziomie rozszerzonym.
							</bold>
							<br />
							<br />
							Zapisy przyjmujemy cay rok szkolny.
							<br />
							<br /> Pierwsze grupy startuj w <bold>padzierniku 2022 r.</bold>
							<br />
							<br />
							Zajcia odbywaj si:
						</p>
						<div
							className='science-classes classes-programming'
							data-aos='fade-up'
						>
							<div>w maych grupch</div>
							<div>raz w tygodniu</div>
							<div>trwaj 90 minut</div>
						</div>
					</div>
				</div>
			</div>

			<div className='science-separator programming-separator'></div>
			<div className='science-lecturers lecturers-programming'>
				<h3>PROWADZCY</h3>
				<div className='lecturers-gif'>
					<img
						src={ProgrammingGif}
						alt='Wiedza i kompetencje + zamiowanie do nauki i nauczania'
					/>
				</div>
				<p className='science-text'>
					Jestemy nauczycielami, ktrzy od <bold>2001 roku</bold> organizuj
					obozy naukowe dla uczniw uzdolnionych w kierunku przedmiotw
					cisych.
					<br />
					<br />
					Mamy <bold>wiedz</bold> i <bold>kompetencje</bold> w zakresie
					wspierania pasji modych ludzi, pokazujc, e powszechnie uznana za
					trudn, matematyka jest niezwykle pikn nauk, jeli przez drog jej
					poznania, przewodnikiem bdzie <bold>mentor</bold>, dla ktrego
					stanowi ona zamiowanie.
				</p>
				<div className='lecturers-container'>
					<div className='lecturer'>
						<div className='lecturer-img'>
							<img
								className='photo'
								src={ProgrammingLecturer}
								alt='Wojciech Malicki'
							/>
							<img
								className='gif gif-1'
								src={ProgrammingLecturerGif}
								alt='E=mc^2'
							/>
						</div>
						<div className='lecturer-info'>
							<br />
							mgr
							<h4>Wojciech Malicki</h4>
						</div>
						<a className='lecturer-more-info more-info-programming'>
							zobacz wicej
							<img src={ScienceArrow} alt='Strzaka zobacz wicej.' />
						</a>
					</div>
					<div className='lecturers-mission'>
						<p className='science-text'>
							<span className='text-small'>Nasza misja brzmi:</span> <br />
							<br />
							<i>
								Osada Wiedzy to przestrze budowania relacji.{' '}
								<br className='br' /> Wyobrania, rozwj i wiedza stanowi
								podstawowe wartoci,
								<br className='br' /> a wiara w moliwoci czowieka wpisana
								jest w serca prowadzcych.
							</i>
						</p>
					</div>
				</div>
				<div className='science-price-list'>
					<h5>programowanie python</h5>
					<p className='science-text'>
						300 z
						<br className='br-reverse' /> miesicznie
					</p>
					<h5>przygotowanie do matury z informatyki</h5>
					<p className='science-text'>
						300 z
						<br className='br-reverse' /> miesicznie
					</p>
				</div>
				<div
					className='lecturers-where'
					style={{ background: `url(${ProgrammingFrame})` }}
				>
					<p className='science-text'>
						Zajcia z programowania prowadzimy w{' '}
						<span className='science-color-programming'>Lborku</span>.
						<br className='br-reverse' />
						<br />
						Uczniowie pracuj na wasnym sprzcie
						<br className='br-reverse' />
						<br />
						Pierwsza lekcja jest prbna i bezpatna w przypadku rezygnacji z
						kursu.
						<br className='br-reverse' />
						<br /> Na yczenie wystawiamy faktury.
					</p>
				</div>
			</div>
			<div className='science-separator programming-separator'></div>
			<ScienceMail color='var(--science-programming)' />
			<Footer />
		</div>
	);
};

export default ScienceProgramming;
