import React from 'react';

import Maths1 from '../../assets/img/maths1.jpg';
import Maths2 from '../../assets/img/maths2.jpg';
import MathsGif from '../../assets/img/mathsGif.gif';
import MathsLecturer from '../../assets/img/mathsLecturer.png';
import MathsLecturerGif from '../../assets/img/mathsLecturerGif.gif';
import ScienceArrow from '../../assets/img/scienceArrow.svg';
import MathsFrame from '../../assets/img/mathsFrame.png';
import { Link } from 'react-router-dom';

import ScienceMail from '../../components/ScienceMail';
import Footer from '../../components/Footer';

const ScienceMaths = () => {
	return (
		<div>
			<div className='science-background science-background--maths'>
				<h2 data-aos='fade-up'>MATEMATYKA</h2>
				<Link to='/nauki-scisle-po-godzinach'>NAUKI CISE PO GODZINACH</Link>
			</div>
			<div className='science-content'>
				<h3>
					Matematyka dla uczniw
					<br className='br-reverse' /> i uczennic klas VIII
				</h3>
				<div className='science-maths section-1'>
					<div className='section'>
						<p className='science-text'>
							Egzamin smoklasisty to pierwszy bardzo wany sprawdzian dla
							modego czowieka, ktrego wynik wpywa na proces rekrutacji do
							szkoy ponadpodstawowej. Uczniowie i uczennice potrzebuj czsto
							wsparcia, zarwno w zakresie zdobywania wiedzy, jak i rozwinicia
							poczucia pewnoci wasnych umiejtnoci, aby stres nie wpyn na
							efekt kocowy.
							<br />
							<br />
							<bold className='science-color-maths-bigger'>
								Nasz kurs skierowany jest do uczniw, ktrzy pragn
								usystematyzowa swoj wiedz, rozwin umiejtnoci w ktrych
								nie czuj si pewni, a take przypomnie sobie zagadnienia z
								wczeniejszych lat szkolnych.
							</bold>
							<br />
							<br />
							<ul>
								Nasze zajcia pozalekcyjne s:
								<li>zaprojektowane z myl o uczniach i uczennicach,</li>
								<li>dostosowane do ich indywidualnych potrzeb,</li>
								<li>nauczane przez wykwalifikowanego nauczyciela</li>
								<li>
									prowadzone w przyjaznej atmosferze wolnej od ocen i popiechu.
								</li>
							</ul>
						</p>
					</div>
					<div className='section section-img'>
						<img src={Maths1} alt='Zajcia z matematyki.' />
					</div>
					<div className='section'>
						<p className='science-text'>
							W dzisiejszych czasach niezalenie od tego, czy planuje si zosta
							psychologiem, lekarzem, czy inynierem z pewnoci potrzebne s
							cho podstawowe umiejtnoci matematyczne. S one bardzo cennymi
							umiejtnociami na wspczesnym rynku pracy.
							<br />
							<br />
							<bold className='science-color-maths-bigger'>
								Analityczne i krytyczne mylenie to wskazywane w wielu raportach
								kluczowe kompetencje przyszoci.
							</bold>
						</p>
					</div>
					<div className='section section-img'>
						<img src={Maths2} alt='Zajcia z matematyki.' />
					</div>
					<div className='section'>
						<p className='science-text science-text-bigger'>
							Zajcia przyjmujemy cay rok szkolny.
							<br />
							<br />
							Pierwsze grupy startuj w <bold>padzierniku 2022 r.</bold>
							<br />
							<br />
							Zajcia odbywaj si:
						</p>
						<div className='science-classes classes-maths' data-aos='fade-up'>
							<div>w maych grupch</div>
							<div>raz w tygodniu</div>
							<div>trwaj 90 minut</div>
						</div>
					</div>
				</div>
			</div>
			<div className='science-separator maths-separator'></div>
			<div className='science-lecturers lecturers-maths'>
				<h3>PROWADZCA</h3>
				<div className='lecturers-gif'>
					<img
						src={MathsGif}
						alt='Wiedza i kompetencje + zamiowanie do nauki i nauczania'
					/>
				</div>
				<p className='science-text'>
					Jestemy nauczycielami, ktrzy od <bold>2001 roku</bold> organizuj
					obozy naukowe dla uczniw uzdolnionych w kierunku przedmiotw
					cisych.
					<br />
					<br />
					Mamy <bold>wiedz</bold> i <bold>kompetencje</bold> w zakresie
					wspierania pasji modych ludzi, pokazujc, e powszechnie uznana za
					trudn, matematyka jest niezwykle pikn nauk, jeli przez drog jej
					poznania, przewodnikiem bdzie <bold>mentor</bold>, dla ktrego
					stanowi ona zamiowanie.
				</p>
				<div className='lecturers-container'>
					<div className='lecturer'>
						<div className='lecturer-img'>
							<img
								className='photo'
								src={MathsLecturer}
								alt='Agata Baranowska'
							/>
							<img className='gif gif-1' src={MathsLecturerGif} alt='E=mc^2' />
						</div>
						<div className='lecturer-info'>
							<br />
							mgr
							<h4>AGATA BARANOWSKA</h4>
						</div>
						<a className='lecturer-more-info more-info-maths'>
							zobacz wicej
							<img src={ScienceArrow} alt='Strzaka zobacz wicej.' />
						</a>
					</div>
					<div className='lecturers-mission'>
						<p className='science-text'>
							<span className='text-small'>Nasza misja brzmi:</span> <br />
							<br />
							<i>
								Osada Wiedzy to przestrze budowania relacji.{' '}
								<br className='br' /> Wyobrania, rozwj i wiedza stanowi
								podstawowe wartoci,
								<br className='br' /> a wiara w moliwoci czowieka wpisana
								jest w serca prowadzcych.
							</i>
						</p>
					</div>
				</div>
				<div className='science-price-list'>
					<h5>przygotowanie do egzaminu smoklasisty</h5>
					<p className='science-text'>
						250 z
						<br className='br-reverse' /> miesicznie
					</p>
				</div>
				<div
					className='lecturers-where'
					style={{ background: `url(${MathsFrame})` }}
				>
					<p className='science-text'>
						Zajcia z matematyki prowadzimy w{' '}
						<span className='science-color-maths'>Lborku</span>.
						<br className='br-reverse' />
						<br />
						Pierwsza lekcja jest prbna i bezpatna w przypadku rezygnacji z
						kursu.
						<br className='br-reverse' />
						<br /> Na yczenie wystawiamy faktury.
					</p>
				</div>
			</div>
			<div className='science-separator maths-separator'></div>
			<ScienceMail color='var(--science-maths)' />
			<Footer />
		</div>
	);
};

export default ScienceMaths;
